<template>
  <div class="search-bar-wrap">
    <form v-on:submit.prevent="searchAlgolia(query)">
      <input type="text" name="search-bar" placeholder="search" v-model="query" id="search-bar">
    </form>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  data() {
    return {
      query: null
    };
  },
  computed: {
    ...mapGetters({})
  },
  methods: {
    ...mapActions({
      searchAlgolia: 'searchAlgolia'
    })
  },
  updated() {
    // this.searchAlgolia();
  }
};
</script>



