      <!--TODO : MAKE THIS PRISMIC-->
      Sample Sale - 75% of all items!
    </div>
    <div id="nav-bar">
      <div id="nav-bar-left" class="nav-bar-group">
        <div class="hamburger" v-on:click="toggleMenu" id="hamburger-icon">
          <div class="bar" :class="{animate : showMenu}"></div>
        </div>
        <search-bar></search-bar>
      </div>

      <router-link to="/">
        <div id="nav-bar-branding" @click="closeMenu">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 602.4 77.6">
