                <path fill="#fff" d="M192.2 77.6H188l-2.6-11-.8-3.7c0 .8-.3 2.4-.6 3.7l-2.7 11h-4l-4.5-21.3h4.5l1.8 10.4.6 3.5.7-3.5 2.5-10.4h3.7l2.5 10.4.8 3.5c0-1 .3-2.5.5-3.5l1.8-10.4h4.4zm51.3 0l-1.7-4.8H234l-1.7 4.8h-4.5l8.1-21.3h4.2l8 21.3zM239 64.9l-1-3.3-1.1 3.3-1.4 3.9h4.9zm43.6 12.7V56.3h4.2v17.1h10.7v4.2zm49.4 0V56.3h4.2v17.1h10.7v4.2zm58.2-7.6h-4.7v7.6h-4.2V56.3h9.3c4 0 7.3 2.2 7.3 6.7 0 5-3.3 7-7.7 7zm.2-9.6h-4.9v5.5h5c2 0 3-1 3-2.8s-1.2-2.7-3-2.7zm54 17.2l-1.7-4.8h-7.8l-1.7 4.8h-4.6l8.2-21.3h4.2l8.1 21.3zm-4.5-12.7l-1.1-3.3-1 3.3-1.4 3.9h4.8zm52.5 5.1h-4.7v7.6h-4.2V56.3h9.3c4 0 7.3 2.2 7.3 6.7 0 5-3.3 7-7.7 7zm.2-9.6h-4.9v5.5h5c2 0 3-1 3-2.8s-1.2-2.7-3-2.7zm42 17.2V56.3h14.8v4.2H539V64h6.1v4.2H539v5.1h11v4.2zm62.7 0l-3.9-7.7h-4.2v7.7h-4.3V56.3h10c4 0 7.3 2.2 7.3 6.7 0 3.2-1.4 5.2-4.1 6.3l4 8.3zm-2.6-17.2h-5.6V66h5.6c2 0 3-1 3-2.8s-1-2.7-3-2.7zM173.1.7h32v10.5H185v5.5h18.4v10.2H185V33h20.8v10.5H173zm87.1 0l-14 42.8h-13.6L218.6.7h12.7l8.1 27.5L247.6.7zm33 43.5c-12.3 0-21.8-9.5-21.8-22S280.9 0 293.2 0s21.7 9.5 21.7 22.1-9.5 22.1-21.7 22.1zm0-11.5c5.4 0 9.8-4.5 9.8-10.6s-4.4-10.5-9.8-10.5-9.9 4.4-9.9 10.5 4.4 10.5 9.9 10.5zM331 .7h11.8V32h18.7v11.5H331zm55.4 0v24.8c0 4.4 2.7 7.2 6.8 7.2s6.8-2.8 6.8-7.2V.7h11.9v24.8c0 11.6-7.2 18.7-18.7 18.7s-18.7-7.1-18.7-18.7V.7zM450 43.5h-11.9V12.2h-11.6V.7h35v11.5H450zm38.4 0h-11.9V.7h11.9zm37.8.7c-12.3 0-21.8-9.5-21.8-22S513.9 0 526.2 0s21.7 9.5 21.7 22.1-9.5 22.1-21.7 22.1zm0-11.5c5.4 0 9.8-4.5 9.8-10.6s-4.4-10.5-9.8-10.5-9.9 4.4-9.9 10.5 4.4 10.5 9.9 10.5zm76.2-32v42.8h-11l-15.6-22v22H564V.7h10.9l15.7 22V.8z" />
                <path fill="#fff" d="M147 10v61.8H24.2L147 10m5.7-9.2L0 77.5h152.7V.8z" />
