              </g>
            </g>
          </svg>
          <!--
          <span class="altero brand-line desktop-only">evolution</span>

          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 429.6 429.6">
            <g data-name="Logo Layer">
              <path class="evolution-svg-logo" d="M0 0v429.6h429.6V0zm405.1 402.7H24.4l380.7-215.9z" data-name="Layer 1" />
            </g>
          </svg>
          <div id="wordmark-container" class=" desktop-only">

          </div>
          <span class="altero brand-line desktop-only">wallpaper</span>
-->

        </div>
      </router-link>

      <div id="nav-bar-right" class="nav-bar-group" @click="closeMenu">
        <span v-if="loggedIn" class="desktop-only">
          <router-link to="/hello">{{userEmail}}</router-link>
        </span>
        <span v-if="loggedIn" class="mobile-only"> profileicon</span>
        <router-link to="cloudinary">RoomPreview</router-link>
        <router-link to="login" v-if="!loggedIn">login</router-link>&nbsp;
        <span v-on:click="logout" v-if="loggedIn">Logout</span>

        <router-link to="/favorites" id="favorites-header-icon">
          <span class="favorites-count">
            <!--{{ getFavoritesCount
          }}-->
          </span>
          <span class=" header-heart icon-heart"></span>
        </router-link>

        <router-link to="/cart" id=" cart-header-icon ">
          <span class="cart-count">
            <!-- {{ getCartCount }}-->
          </span>
          <span class="icon-cart header-cart"></span>
        </router-link>
      </div>

    </div>
    <transition name="fade">
      <div class="fullsize-menu" v-show="showMenu">
        <ul>
          <li>
            <span @click="closeMenu">
              <router-link to="/" id="home-link"> home </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/artists" id="artist-link"> artists </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/blog" id="blog-link"> blog </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/education" id="education-link"> education </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/wallpaper" id="wallpaper-link"> wallpaper </router-link>
            </span>
          </li>
