</template>

<script>
import firebase from 'firebase';
import SearchBar from './navbar/SearchBar';

export default {
  components: {
    SearchBar
  },
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      userEmail: '',
      userId: '',
      loggedIn: null,
      showMenu: false
    };
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace('login');
        });
    },
    checkUser: function() {
      var user = firebase.auth().currentUser;

      if (user) {
        console.log(user);
        this.userEmail = user.email;
        this.loggedIn = true;
        // User is signed in.
      } else {
        console.log(user);
        this.userEmail = '';
        this.loggedIn = false;

        // No user is signed in.
      }
      /*    firebase.auth().onAuthStateChanged(function(user) {
         if (user) {
         this.nameFromServer = user.fullname;
         console.log(user)
         } else {
         console.log(user)

         // No user is signed in.
         }
         });*/
      /*
         let userId = firebase.auth().currentUser.uid;

         return firebase.database().ref('/users/' + userId).once('value').then(snapshot => {
         this.nameFromServer = (snapshot.val() && snapshot.val().fullName) || 'Anonymous';
         });*/
    },
    closeMenu() {
      this.showMenu = false;
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    }
  },
  mounted() {
    this.checkUser();
  },
  watch: {
    $route(to, from) {
      this.checkUser();
    }
  }
};
</script>



