<template>
  <div class="artist page" id="artist-page">
    <div class="container" id="artist-page-container">
      <div class="content-block">
        <router-link to="/artists" class="button" id="artist-back-btn">Back</router-link>
        <div class="title" id="artist-title">{{pageArtist.data.artist_name[0].text}}</div>
        <img :src="`${pageArtist.data.artist_picture.url}`" :alt="`${pageArtist.data.artist_name[0].text}`"><br>
        <serializer id="artist-page-serializer" :content='pageArtist.data.artist_bio'></serializer>
        <artist-patterns :patterns='artistPatterns'></artist-patterns>
      </div>
    </div>
    <hr>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Serializer from './../serializer/Serializer';
import ArtistPatterns from './ArtistPatterns';

export default {
  components: { Serializer, ArtistPatterns },
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    };
  },
  computed: {
    ...mapGetters({
      pageArtist: 'getPageArtistFromStore',
      artistPatterns: 'getArtistPatternsFromStore'
    })
  },
  methods: {
    ...mapActions({
      getPageArtist: 'getPageArtist',
      getArtistPatterns: 'getArtistPatterns'
    })
  },
  mounted() {
    this.getPageArtist(this.$route.params.artist);
    window.scrollTo(0, 0);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



