<template>
  <div id="artist-patterns">
    <h2 v-if="patterns.length">Patterns by this artist:</h2>
    <div class="patterns-wrap">
      <wallpaper-pattern :result="pattern" v-for="pattern in patterns"></wallpaper-pattern>
    </div>
  </div>
</template>

<script>
import WallpaperPattern from './../wallpaper/WallpaperPattern';

export default {
  props: ['patterns'],
  components: {
    WallpaperPattern
  }
};
</script>



